//font size
$html-font-size : 1.125rem;
$body-font-size : 1.125rem; // 18px


$mobile  : 360px;	//
$tablet  : 750px;	//
$pc      : 1200px;	//pc   :     
$pc-wide : 1440px;	//pc  
//$pc-wide : 1680px;
$xs      : max-width $mobile - 1; // 359 
$sm      : max-width $tablet - 1; // 749 
$md      : $tablet;               // 750 
$smd     : max-width  $pc - 1;    // 1199 
$mdw     : $tablet $pc - 1;       // 750 ~ 119
$lg      : $pc;                   // 1200 
$xl      : $pc-wide;              // 1680 


